header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--pink);
  color: var(--light-blue);
  padding: 0 10px;
  box-shadow: 0px 2px 10px -5px rgba(21, 6, 5, 1);

  & > .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .cart-wrap {
    div {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background-color: var(--light-blue);
      font-size: 9px;
      color: var(--black);
      min-width: 15px;
      aspect-ratio: 1;

    }
  }
}
