*, *::before, *::after {
  box-sizing: border-box;
}


html, body {
  font-family: 'Oswald', sans-serif;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  &:visited { color: var(--light-blue); }
  &:hover { color: var(--light-blue); }
  &:active { color: var(--light-blue); }
}

